import { useMemo } from "react"
import { BriefConfig } from "@rp/react/tanstack-api/brief"

const emptyBriefConfigs: Array<BriefConfig> = []

type UseSievedUserBriefConfigsProps = {
  userBriefConfigs: Array<BriefConfig> | undefined
  filterTerm: string
}

type UseSievedUserBriefConfigsState = {
  sievedUserBriefConfigs: Array<BriefConfig>
}

const useSievedUserBriefConfigs = ({
  userBriefConfigs,
  filterTerm,
}: UseSievedUserBriefConfigsProps): UseSievedUserBriefConfigsState => {
  const sievedUserBriefConfigs = useMemo(() => {
    if (!userBriefConfigs) return emptyBriefConfigs

    const normalizedFilterTerm = filterTerm.toLowerCase()

    return userBriefConfigs
      .filter(config => config.briefName.toLocaleLowerCase().includes(normalizedFilterTerm))
      .sort((a, b) => b.dateCreated?.localeCompare(a.dateCreated)) // TODO: remove optional-chaining once API fixed
  }, [filterTerm, userBriefConfigs])

  return { sievedUserBriefConfigs }
}

export { useSievedUserBriefConfigs }
export type { UseSievedUserBriefConfigsProps, UseSievedUserBriefConfigsState }
